<template>
  <div class="box">
         <mt-header title="">
  <router-link to=" " slot="left">
    <mt-button icon="back" @click="goback"></mt-button>
  </router-link>
  <mt-button icon="more" slot="right"></mt-button>
</mt-header>
    <van-checkbox-group class="card-goods" v-model="checkedGoods">
      <van-checkbox
        class="card-goods__item"
        v-for="item in goods"
        :key="item.id"
        :name="item.id"
      >
        <van-card
          :title="item.title"
          :desc="item.desc"
          :num="item.num"
          :price="formatPrice(item.price)"
          :thumb="item.thumb"
        />
      </van-checkbox>
    </van-checkbox-group>
    <van-submit-bar
      :price="totalPrice"
      :disabled="!checkedGoods.length"
      :button-text="submitBarText"
      @submit="onSubmit"
    />
  </div>
</template>

<script>
import { Checkbox, CheckboxGroup, Card, SubmitBar, Toast } from 'vant';
import 'vant/lib/checkbox/style';
import 'vant/lib/checkbox-group/style';
import 'vant/lib/card/style';
import 'vant/lib/submit-bar/style';
import 'vant/lib/toast/style';
export default {
  components: {
    [Card.name]: Card,
    [Checkbox.name]: Checkbox,
    [SubmitBar.name]: SubmitBar,
    [CheckboxGroup.name]: CheckboxGroup
  },
  data() {
    return {
      checkedGoods: ['1', '2', '3'],
      goods: [{
        id: '1',
        title: 'lens',
        desc: '1',
        price: 11000,
        num: 1,
        thumb: 'https://img.alicdn.com/imgextra/i2/115337254/O1CN01dabIG223SNXUoNUFm_!!0-saturn_solar.jpg_240x240xz.jpg_.webp'
      }, {
        id: '2',
        title: 'ES6 3 ',
        desc: '1',
        price: 9900,
        num: 1,
        thumb: 'https://img.alicdn.com/imgextra/i2/2598292358/TB2cPwEiL6TBKNjSZJiXXbKVFXa_!!2598292358.jpg_430x430q90.jpg'
      }, {
        id: '3',
        title: ' ',
        desc: '1',
        price: 6800,
        num: 1,
        thumb: 'https://img.alicdn.com/imgextra/i3/370051719/O1CN01V0J4kA1OZLOKJ8OLL_!!370051719.jpg_60x60q90.jpg'
      }]
    };
  },
  computed: {
    submitBarText() {
      const count = this.checkedGoods.length;
      return '' + (count ? `(${count})` : '');
    },
    totalPrice() {
      return this.goods.reduce((total, item) => total + (this.checkedGoods.indexOf(item.id) !== -1 ? item.price : 0), 0);
    }
  },
  methods: {
    formatPrice(price) {
      return (price / 100).toFixed(2);
    },
           goback(){
      this.$router.go(-1)
    },
    onSubmit() {
      Toast('');
    }
  }
};
</script>

<style lang="less">

.card-goods {
  padding: 10px 0;
  background-color: #fff;
  &__item {
    position: relative;
    background-color: #fafafa;
    .van-checkbox__label {
      width: 100%;
      height: auto; // temp
      padding: 0 10px 0 15px;
      box-sizing: border-box;
    }
    .van-checkbox__icon {
      top: 50%;
      left: 10px;
      z-index: 1;
      position: absolute;
      margin-top: -10px;
    }
    .van-card__price {
      color: #f44;
    }
  }
}
</style>